import React, { useEffect, useState } from "react";
import axios from "axios";

function Reports() {
  const [items, setItems] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchItems = async () => {
      try {
        const response = await axios.get("http://127.0.0.1:5000/items");

        // Handle both array or object response structures
        const data = Array.isArray(response.data)
          ? response.data
          : response.data.items;

        setItems(data || []);
      } catch (error) {
        console.error("Error fetching items:", error);
        setItems([]);
      } finally {
        setLoading(false);
      }
    };

    fetchItems();
  }, []);

  if (loading) {
    return <p>Loading reports...</p>;
  }

  if (!Array.isArray(items) || items.length === 0) {
    return <p>No data available for reports.</p>;
  }

  // Calculate totals
  const totalItems = items.length;
  const totalQuantity = items.reduce((acc, item) => acc + Number(item.quantity || 0), 0);
  const totalValue = items.reduce(
    (acc, item) => acc + Number(item.quantity || 0) * Number(item.price || 0),
    0
  );

  return (
    <div className="container mt-4">
      <h2 className="mb-4">Inventory Reports</h2>

      <div className="row mb-4">
        <div className="col-md-4">
          <div className="card text-center shadow-sm">
            <div className="card-body">
              <h5>Total Items</h5>
              <h3>{totalItems}</h3>
            </div>
          </div>
        </div>

        <div className="col-md-4">
          <div className="card text-center shadow-sm">
            <div className="card-body">
              <h5>Total Quantity</h5>
              <h3>{totalQuantity}</h3>
            </div>
          </div>
        </div>

        <div className="col-md-4">
          <div className="card text-center shadow-sm">
            <div className="card-body">
              <h5>Total Value</h5>
              <h3>Ksh {totalValue.toLocaleString()}</h3>
            </div>
          </div>
        </div>
      </div>

      <table className="table table-striped table-bordered shadow-sm">
        <thead className="table-dark">
          <tr>
            <th>ID</th>
            <th>Item Name</th>
            <th>Quantity</th>
            <th>Price (Ksh)</th>
            <th>Total Value</th>
          </tr>
        </thead>
        <tbody>
          {items.map((item) => (
            <tr key={item.id}>
              <td>{item.id}</td>
              <td>{item.name}</td>
              <td>{item.quantity}</td>
              <td>{item.price}</td>
              <td>{(item.quantity * item.price).toLocaleString()}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}

export default Reports;
