import React, { useEffect, useState, useCallback } from "react";
import "../styles/Inventory.css";

const JobCards = () => {
  const [jobs, setJobs] = useState([]);
  const [filteredJobs, setFilteredJobs] = useState([]);
  const [formData, setFormData] = useState({
    customer_name: "",
    vehicle_reg: "",
    service: "",
    status: "Pending",
    cost: ""
  });
  const [selectedStatus, setSelectedStatus] = useState("All");
  const [searchTerm, setSearchTerm] = useState("");
  const [showEditModal, setShowEditModal] = useState(false);
  const [editingJob, setEditingJob] = useState(null);
  const [editFormData, setEditFormData] = useState({
    customer_name: "", vehicle_reg: "", service: "", status: "", cost: ""
  });
  const [showDeleteModal, setShowDeleteModal] = useState(false);
  const [deletingJob, setDeletingJob] = useState(null);

  // GET TOKEN FRESH EVERY TIME
  const getToken = () => localStorage.getItem("token");

  const fetchJobs = useCallback(async () => {
    const token = getToken();
    if (!token) {
      alert("Not logged in. Redirecting to login...");
      window.location.href = "/login";
      return;
    }

    try {
      const res = await fetch("http://127.0.0.1:5000/jobs", {
        headers: { "Authorization": `Bearer ${token}` }
      });

      if (res.status === 401) {
        alert("Session expired. Please log in again.");
        localStorage.removeItem("token");
        window.location.href = "/login";
        return;
      }

      if (!res.ok) throw new Error(`HTTP ${res.status}`);
      const data = await res.json();
      const items = Array.isArray(data) ? data : data.jobs || [];
      setJobs(items);
      setFilteredJobs(items);
    } catch (err) {
      console.error("Fetch error:", err);
      alert("Failed to load jobs. Check console.");
    }
  }, []);

  useEffect(() => {
    fetchJobs();
  }, [fetchJobs]);

  useEffect(() => {
    let filtered = jobs;

    if (searchTerm) {
      filtered = filtered.filter(job =>
        job.customer_name.toLowerCase().includes(searchTerm.toLowerCase()) ||
        job.vehicle_reg.toLowerCase().includes(searchTerm.toLowerCase())
      );
    }

    if (selectedStatus !== "All") {
      filtered = filtered.filter(j => j.status === selectedStatus);
    }

    setFilteredJobs(filtered);
  }, [searchTerm, selectedStatus, jobs]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  // ADD JOB
  const handleSubmit = async (e) => {
    e.preventDefault();
    const token = getToken();
    if (!token) return alert("Please log in");

    const payload = {
      customer_name: formData.customer_name.trim(),
      vehicle_reg: formData.vehicle_reg.trim(),
      service: formData.service.trim(),
      cost: parseFloat(formData.cost),
      status: formData.status
    };

    if (!payload.customer_name || !payload.vehicle_reg || !payload.service) {
      return alert("Fill all fields");
    }
    if (isNaN(payload.cost) || payload.cost <= 0) {
      return alert("Valid cost required");
    }

    try {
      const res = await fetch("http://127.0.0.1:5000/jobs", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "Authorization": `Bearer ${token}`
        },
        body: JSON.stringify(payload)
      });

      const result = await res.json();

      if (!res.ok) throw new Error(result.error || "Add failed");

      setFormData({ customer_name: "", vehicle_reg: "", service: "", status: "Pending", cost: "" });
      await fetchJobs();
      alert("Job added!");
    } catch (err) {
      alert(`Add failed: ${err.message}`);
    }
  };

  const handleFilterChange = (e) => setSelectedStatus(e.target.value);

  // DELETE
  const openDeleteModal = (job) => {
    setDeletingJob(job);
    setShowDeleteModal(true);
  };

  const closeDeleteModal = () => {
    setShowDeleteModal(false);
    setDeletingJob(null);
  };

  const confirmDelete = async () => {
    const token = getToken();
    if (!token || !deletingJob) return;

    try {
      const res = await fetch(`http://127.0.0.1:5000/jobs/${deletingJob.id}`, {
        method: "DELETE",
        headers: { "Authorization": `Bearer ${token}` }
      });

      if (res.status === 401) {
        alert("Session expired. Logging out...");
        localStorage.removeItem("token");
        window.location.href = "/login";
        return;
      }

      if (!res.ok) {
        const error = await res.json();
        throw new Error(error.error || "Delete failed");
      }

      await fetchJobs();
      closeDeleteModal();
      alert("Job deleted!");
    } catch (err) {
      alert(`Delete failed: ${err.message}`);
    }
  };

  // EDIT
  const openEditModal = (job) => {
    setEditingJob(job);
    setEditFormData({
      customer_name: job.customer_name,
      vehicle_reg: job.vehicle_reg,
      service: job.service,
      status: job.status,
      cost: job.cost
    });
    setShowEditModal(true);
  };

  const closeEditModal = () => {
    setShowEditModal(false);
    setEditingJob(null);
  };

  const handleEditChange = (e) => {
    const { name, value } = e.target;
    setEditFormData({ ...editFormData, [name]: value });
  };

  const handleEditSubmit = async (e) => {
    e.preventDefault();
    const token = getToken();
    if (!token || !editingJob) return;

    const payload = {
      customer_name: editFormData.customer_name.trim(),
      vehicle_reg: editFormData.vehicle_reg.trim(),
      service: editFormData.service.trim(),
      cost: parseFloat(editFormData.cost),
      status: editFormData.status
    };

    if (!payload.customer_name || !payload.vehicle_reg || !payload.service) {
      return alert("Fill all fields");
    }
    if (isNaN(payload.cost) || payload.cost <= 0) {
      return alert("Valid cost required");
    }

    try {
      const res = await fetch(`http://127.0.0.1:5000/jobs/${editingJob.id}`, {
        method: "PUT",
        headers: {
          "Content-Type": "application/json",
          "Authorization": `Bearer ${token}`
        },
        body: JSON.stringify(payload)
      });

      const result = await res.json();

      if (res.status === 401) {
        alert("Session expired. Logging out...");
        localStorage.removeItem("token");
        window.location.href = "/login";
        return;
      }

      if (!res.ok) throw new Error(result.error || "Update failed");

      await fetchJobs();
      closeEditModal();
      alert("Job updated!");
    } catch (err) {
      alert(`Update failed: ${err.message}`);
    }
  };

  const statuses = ["All", "Pending", "In Progress", "Completed"];

  return (
    <div className="inventory-container">
      <h1 className="inventory-title">Job Cards</h1>

      {/* LOGIN WARNING */}
      {!getToken() && (
        <div style={{ color: "red", padding: "1rem", background: "#fee", borderRadius: "8px", marginBottom: "1rem" }}>
          Not logged in. <a href="/login" style={{ color: "#3b82f6", fontWeight: "bold" }}>Log in now</a>
        </div>
      )}

      <form onSubmit={handleSubmit} className="add-part-form">
        <input name="customer_name" placeholder="Customer Name" value={formData.customer_name} onChange={handleChange} required />
        <input name="vehicle_reg" placeholder="Vehicle Reg" value={formData.vehicle_reg} onChange={handleChange} required />
        <input name="service" placeholder="Service" value={formData.service} onChange={handleChange} required />
        <input name="cost" type="number" placeholder="Cost (KSh)" value={formData.cost} onChange={handleChange} required />
        <select name="status" value={formData.status} onChange={handleChange}>
          <option value="Pending">Pending</option>
          <option value="In Progress">In Progress</option>
          <option value="Completed">Completed</option>
        </select>
        <button type="submit" className="add-part-btn">Add Job</button>
      </form>

      <div className="flex flex-col md:flex-row gap-4 mb-6">
        <div className="search-bar flex-1">
          <input
            placeholder="Search by customer or vehicle..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
        </div>
        <div className="filter-bar">
          <label>Filter by Status:</label>
          <select value={selectedStatus} onChange={handleFilterChange}>
            {statuses.map(s => (
              <option key={s} value={s}>{s}</option>
            ))}
          </select>
        </div>
      </div>

      <table className="inventory-table">
        <thead>
          <tr>
            <th>Customer</th>
            <th>Vehicle</th>
            <th>Service</th>
            <th>Cost (KSh)</th>
            <th>Status</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          {filteredJobs.length === 0 ? (
            <tr><td colSpan="6" className="no-parts">No jobs found.</td></tr>
          ) : (
            filteredJobs.map(job => (
              <tr key={job.id}>
                <td>{job.customer_name}</td>
                <td>{job.vehicle_reg}</td>
                <td>{job.service}</td>
                <td>{job.cost}</td>
                <td className={`status-cell ${
                  job.status === "Completed" ? 'available' :
                  job.status === "In Progress" ? 'borrowed' : 'low-stock'
                }`}>
                  {job.status}
                </td>
                <td>
                  <button onClick={() => openEditModal(job)} className="edit-btn">Edit</button>
                  <button onClick={() => openDeleteModal(job)} className="delete-btn">Delete</button>
                </td>
              </tr>
            ))
          )}
        </tbody>
      </table>

      {/* DELETE MODAL */}
      {showDeleteModal && (
        <div className="modal-overlay" onClick={closeDeleteModal}>
          <div className="modal" onClick={(e) => e.stopPropagation()}>
            <h3>Delete Job?</h3>
            <p>Delete <strong>{deletingJob?.customer_name}</strong>?</p>
            <div className="modal-buttons">
              <button onClick={closeDeleteModal} className="modal-btn cancel">Cancel</button>
              <button onClick={confirmDelete} className="modal-btn delete">Delete</button>
            </div>
          </div>
        </div>
      )}

      {/* EDIT MODAL */}
      {showEditModal && (
        <div className="modal-overlay" onClick={closeEditModal}>
          <div className="modal" onClick={(e) => e.stopPropagation()}>
            <h3>Edit Job</h3>
            <form onSubmit={handleEditSubmit} className="edit-form">
              <input name="customer_name" value={editFormData.customer_name} onChange={handleEditChange} required />
              <input name="vehicle_reg" value={editFormData.vehicle_reg} onChange={handleEditChange} required />
              <input name="service" value={editFormData.service} onChange={handleEditChange} required />
              <input name="cost" type="number" value={editFormData.cost} onChange={handleEditChange} required />
              <select name="status" value={editFormData.status} onChange={handleEditChange}>
                <option value="Pending">Pending</option>
                <option value="In Progress">In Progress</option>
                <option value="Completed">Completed</option>
              </select>
              <div className="modal-buttons">
                <button type="button" onClick={closeEditModal} className="modal-btn cancel">Cancel</button>
                <button type="submit" className="modal-btn" style={{ background: '#3b82f6' }}>Save</button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
};

export default JobCards;