import React, { useEffect, useState } from "react";
import "../styles/Inventory.css";

const Tools = () => {
  const [tools, setTools] = useState([]);
  const [filteredTools, setFilteredTools] = useState([]);
  const [formData, setFormData] = useState({
    name: "",
    category: "",
    status: "Available",
    borrower: "",
  });
  const [selectedCategory, setSelectedCategory] = useState("All");
  const [searchTerm, setSearchTerm] = useState("");
  const [showEditModal, setShowEditModal] = useState(false);
  const [editingTool, setEditingTool] = useState(null);
  const [editFormData, setEditFormData] = useState({
    name: "",
    category: "",
    status: "",
    borrower: "",
  });
  const [showDeleteModal, setShowDeleteModal] = useState(false);
  const [deletingTool, setDeletingTool] = useState(null);

  const fetchTools = async () => {
    try {
      const res = await fetch("http://127.0.0.1:5000/tools");
      const data = await res.json();
      const items = Array.isArray(data) ? data : data.tools || [];
      setTools(items);
      setFilteredTools(items);
    } catch (err) {
      console.error("Error:", err);
    }
  };

  useEffect(() => { fetchTools(); }, []);

  // Filter by search + category
  useEffect(() => {
    let filtered = tools;

    if (searchTerm) {
      filtered = filtered.filter(tool =>
        tool.name.toLowerCase().includes(searchTerm.toLowerCase())
      );
    }

    if (selectedCategory !== "All") {
      filtered = filtered.filter(t =>
        (t.category || "Uncategorized") === selectedCategory
      );
    }

    setFilteredTools(filtered);
  }, [searchTerm, selectedCategory, tools]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      await fetch("http://127.0.0.1:5000/tools", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(formData),
      });
      setFormData({ name: "", category: "", status: "Available", borrower: "" });
      fetchTools();
    } catch (err) {
      console.error("Error:", err);
    }
  };

  const handleFilterChange = (e) => {
    setSelectedCategory(e.target.value);
  };

  const openDeleteModal = (tool) => {
    setDeletingTool(tool);
    setShowDeleteModal(true);
  };

  const closeDeleteModal = () => {
    setShowDeleteModal(false);
    setDeletingTool(null);
  };

  const confirmDelete = async () => {
    if (!deletingTool) return;
    try {
      await fetch(`http://127.0.0.1:5000/tools/${deletingTool.id}`, { method: "DELETE" });
      fetchTools();
      closeDeleteModal();
    } catch (err) {
      console.error("Delete error:", err);
    }
  };

  const openEditModal = (tool) => {
    setEditingTool(tool);
    setEditFormData({
      name: tool.name,
      category: tool.category || "",
      status: tool.status,
      borrower: tool.borrower || ""
    });
    setShowEditModal(true);
  };

  const closeEditModal = () => {
    setShowEditModal(false);
    setEditingTool(null);
  };

  const handleEditChange = (e) => {
    const { name, value } = e.target;
    setEditFormData({ ...editFormData, [name]: value });
  };

  const handleEditSubmit = async (e) => {
    e.preventDefault();
    if (!editingTool) return;
    try {
      await fetch(`http://127.0.0.1:5000/tools/${editingTool.id}`, {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(editFormData),
      });
      fetchTools();
      closeEditModal();
    } catch (err) {
      console.error("Edit error:", err);
    }
  };

  const categories = ["All", ...new Set(tools.map(t => t.category || "Uncategorized"))];

  return (
    <div className="inventory-container">
      <h1 className="inventory-title">Tools Inventory</h1>

      {/* Add Tool Form */}
      <form onSubmit={handleSubmit} className="add-part-form">
        <input name="name" placeholder="Tool Name" value={formData.name} onChange={handleChange} required />
        <input name="category" placeholder="Category" value={formData.category} onChange={handleChange} required />
        <select name="status" value={formData.status} onChange={handleChange}>
          <option value="Available">Available</option>
          <option value="Borrowed">Borrowed</option>
        </select>
        <input name="borrower" placeholder="Borrower Name (if borrowed)" value={formData.borrower} onChange={handleChange} />
        <button type="submit" className="add-part-btn">Add Tool</button>
      </form>

      {/* Search + Filter */}
      <div className="flex flex-col md:flex-row gap-4 mb-6">
        <div className="search-bar flex-1">
          <input placeholder="Search tools by name..." value={searchTerm} onChange={(e) => setSearchTerm(e.target.value)} />
        </div>
        <div className="filter-bar">
          <label>Filter by Category:</label>
          <select value={selectedCategory} onChange={handleFilterChange}>
            {categories.map(cat => <option key={cat} value={cat}>{cat}</option>)}
          </select>
        </div>
      </div>

      {/* Table */}
      <table className="inventory-table">
        <thead>
          <tr>
            <th>Tool Name</th>
            <th>Category</th>
            <th>Status</th>
            <th>Borrower</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          {filteredTools.length === 0 ? (
            <tr><td colSpan="5" className="no-parts">No tools found.</td></tr>
          ) : (
            filteredTools.map(tool => (
              <tr key={tool.id}>
                <td>{tool.name}</td>
                <td>{tool.category || "Uncategorized"}</td>
                <td className={`quantity-cell ${tool.status === "Borrowed" ? 'low-stock' : ''}`}>
                  {tool.status}
                </td>
                <td>{tool.borrower || ""}</td>
                <td>
                  <button onClick={() => openEditModal(tool)} className="edit-btn">Edit</button>
                  <button onClick={() => openDeleteModal(tool)} className="delete-btn">Delete</button>
                </td>
              </tr>
            ))
          )}
        </tbody>
      </table>

      {/* Delete Modal */}
      {showDeleteModal && (
        <div className="modal-overlay" onClick={closeDeleteModal}>
          <div className="modal" onClick={(e) => e.stopPropagation()}>
            <h3>Delete Tool?</h3>
            <p>Are you sure you want to delete <strong>{deletingTool?.name}</strong>?</p>
            <div className="modal-buttons">
              <button onClick={closeDeleteModal} className="modal-btn cancel">Cancel</button>
              <button onClick={confirmDelete} className="modal-btn delete">Delete</button>
            </div>
          </div>
        </div>
      )}

      {/* Edit Modal */}
      {showEditModal && (
        <div className="modal-overlay" onClick={closeEditModal}>
          <div className="modal" onClick={(e) => e.stopPropagation()}>
            <h3>Edit Tool</h3>
            <form onSubmit={handleEditSubmit} className="edit-form">
              <input name="name" value={editFormData.name} onChange={handleEditChange} required />
              <input name="category" value={editFormData.category} onChange={handleEditChange} required />
              <select name="status" value={editFormData.status} onChange={handleEditChange}>
                <option value="Available">Available</option>
                <option value="Borrowed">Borrowed</option>
              </select>
              <input name="borrower" placeholder="Borrower Name" value={editFormData.borrower} onChange={handleEditChange} />
              <div className="modal-buttons">
                <button type="button" onClick={closeEditModal} className="modal-btn cancel">Cancel</button>
                <button type="submit" className="modal-btn" style={{ background: '#3b82f6' }}>Save</button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
};

export default Tools;